
import {
  CButton,
  CCard,
  CCardHeader,
  CCol,
  CRow,
  CTooltip,
  CInput,CForm,
  CModal,CModalBody,CModalFooter,CModalHeader,CModalTitle,CFormGroup,CLabel,CSelect
} from "@coreui/react";
import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import {
  GetIndikatorsAction,
  setVariabelId,
  setFormIndikatorAction,
  PostIndikatorAction,
  GetIndikatorByIdAction,
  UpdateIndikatorAction,
  DeleteIndikator,
} from "src/common/redux/action";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { CardBody } from 'reactstrap';
import MaterialTable from 'material-table';
import { useHistory, useLocation } from "react-router-dom";
import CIcon from "@coreui/icons-react";
import Select from "react-select";
import makeAnimated from "react-select/animated";



const Indikator = ({match}) => {

  const dataComboTypes = [
  { value: '0', label: 'Pilih ' },
  { value: '1', label: 'Type 1' },
  { value: '2', label: 'Type 2' },
  { value: '3', label: 'Type 3' },
  { value: '4', label: 'Type 4' },
]
  const animatedComponents = makeAnimated();
  const history = useHistory();
   let idVariabel = match.params.id;
     const [stateUpdate, setStateUpdate] = useState(false);
   /**
    * start initialize reducer
    */
   const dispatch = useDispatch();
   const {
     GetListIndikatorReducer,
     modalIndikator,
     formIndikatorReducer,
   } = useSelector((state) => state.indikatorReducer);
   const { IdIndikator, IdVariabel, Nama, Type } = formIndikatorReducer;
   const { formVariabel } = useSelector((state) => state.masterVariabelReducer);


   const startLoad = () =>{
      dispatch(GetIndikatorsAction("GET_INDIKATORS", idVariabel));
      dispatch(setVariabelId(idVariabel));
   };

   const showModal = () =>
     dispatch({ type: "MODAL_INDIKATOR", payload: !modalIndikator });
   // load modal
   const handleModalIndikator = (type, idIndikator)=>{

       if (type == "add") {
         dispatch({ type: "RESET_FORM_INDIKATOR" });

         showModal();
         setStateUpdate(false);

       } else {
         dispatch(GetIndikatorByIdAction(idIndikator));
         showModal();
         setStateUpdate(true);
       }
        dispatch(setFormIndikatorAction("IdVariabel", idVariabel));


   }

   const prosesIndikatorAdd = () =>
     dispatch(PostIndikatorAction(formIndikatorReducer));
   const prosesIndikatorEdit = () =>
     dispatch(UpdateIndikatorAction(formIndikatorReducer));
   const prosesDelete = (idIndikator, nama) =>{
      let idVar = match.params.id;
      console.log(idVar)
     dispatch(DeleteIndikator(idIndikator, nama, idVar));
   }

   const columns = [
     {
       title: "Aksi",
       field: "internal_action",
       cellStyle: {
         backgroundColor: "#039be5",
         color: "#FFF",
       },
       headerStyle: {
         backgroundColor: "#039be5",
       },
       render: (rowData) => {
         return (
           <div className="row d-flex justify-content-center">
             <CTooltip content="Detail Pertanyaan">
               <button
                 type="button"
                 className="btn btn-sm btn-warning text-center mr-2"
                 onClick={() =>
                   history.push(`/pertanyaan/${rowData.idIndikator}`)
                 }
               >
                 <CIcon name="cil-align-right" />
                 Detail Pertanyaan
               </button>
             </CTooltip>
             <CTooltip content="Edit Indikator">
               <button
                 type="button"
                 className="btn btn-sm btn-dark text-center mr-2"
                 onClick={() =>
                   handleModalIndikator("edit", rowData.idIndikator)
                 }
               >
                 <CIcon name="cil-pencil" /> Update
               </button>
             </CTooltip>
             <CTooltip content="Hapus Indikator">
               <button
                 type="button"
                 className="btn btn-sm btn-danger"
                 onClick={() => prosesDelete(rowData.idIndikator, rowData.nama)}
               >
                 <CIcon name="cil-drop" /> Hapus
               </button>
             </CTooltip>
           </div>
         );
       },
     },
     { title: "Indikator Evaluasi", field: "nama" },
     { title: "Type ", field: "type" },
   ];

   useEffect(() => {
    startLoad();
   }, [dispatch]);
   return (
     <div>
       <CRow>
         <CCol md="12">
           <CCard>
             <CCardHeader>
               <CRow>
                 <CCol md="9">
                   <h4>
                     <small>Variabel</small> {formVariabel.Nama}
                   </h4>
                 </CCol>
                 <CCol md="3">
                   <CRow>
                     <CCol md="5">
                       <CButton color="dark" variant="outline">
                         Kembali
                       </CButton>
                     </CCol>

                     <CCol md="6">
                       <CButton
                         color="primary"
                         onClick={() => handleModalIndikator("add")}
                       >
                         <FontAwesomeIcon icon="plus" />
                         Tambah
                       </CButton>
                     </CCol>
                   </CRow>
                 </CCol>
               </CRow>
             </CCardHeader>
             <CardBody>
               <MaterialTable
                 title=""
                 options={{
                   headerStyle: {
                     backgroundColor: "#01579b",
                     color: "#FFF",
                   },
                   filtering: true,
                 }}
                 columns={columns}
                 data={GetListIndikatorReducer}
               />
             </CardBody>
           </CCard>
         </CCol>
       </CRow>

       <CModal show={modalIndikator} onClose={showModal} size="lg">
         <CModalHeader closeButton>
           <CModalTitle>
             {" "}
             {stateUpdate ? "Form Update" : "Form Tambah"}
           </CModalTitle>
         </CModalHeader>
         <CModalBody>
           <CForm>
             <CFormGroup>
               <CLabel htmlFor="nf-nama">Nama</CLabel>
               <CInput
                 type="text"
                 id="nf-kode"
                 name="nama"
                 placeholder="input nama indikator ..."
                 value={Nama}
                 onChange={(e) =>
                   dispatch(setFormIndikatorAction("Nama", e.target.value))
                 }
               />
             </CFormGroup>
             <CSelect
               custom
               name="filter_type"
               id="filter_type"
               value={Type}
               onChange={(e) => dispatch(setFormIndikatorAction("Type", e.target.value))}
             >
               <option value="0"> --Pilih Type -- </option>
               {dataComboTypes.map((v) => (
                 <option key={v.value} value={v.value}>
                   {v.label}
                 </option>
               ))}
             </CSelect>
           </CForm>
         </CModalBody>
         <CModalFooter>
           <CButton
             color="primary"
             onClick={stateUpdate ? prosesIndikatorEdit : prosesIndikatorAdd}
           >
             {stateUpdate ? "Update" : "Tambah"}
           </CButton>
           <CButton color="secondary" onClick={showModal}>
             Cancel
           </CButton>
         </CModalFooter>
       </CModal>
     </div>
   );
}


export default Indikator;
